<template>
  <v-container>
    <v-row 
      justify="center"
      class="flex-column"
    >
      <v-col>
        <v-card 
          max-width="700"
          class="mx-auto mb-3"
          dark
          style="background: linear-gradient(to bottom left, rgba(100,115,201, .5), rgba(25,32,72, .74));"
        >
          <v-row class="fill-height pa-3">
            <v-col>
              <v-avatar
                color="grey"
                size="117"
              >
                <v-img src="https://cdn.vuetifyjs.com/images/john.jpg"></v-img>
              </v-avatar>
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title class="title">{{ user.userName }}</v-list-item-title>
                  <v-list-item-subtitle>Id: {{ id }}</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-col>
            <v-col 
              cols="12"
              sm="6"
              class="mx-auto"
            >
              <v-text-field
                v-model="user.userName"
                label="Имя"
                required
              ></v-text-field>

              <v-text-field
                v-model="id"
                label="ID пользователя"
                required
              ></v-text-field>

              <v-text-field
                v-model="user.balance"
                label="Баланс"
                required
              ></v-text-field>
              
              <v-select
                v-model="user.userRole"
                :items="roles"
                label="Роль"
                required
              ></v-select>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
      <v-col>
        <v-card 
          max-width="700"
          class="mx-auto mb-3"
        >
          <v-card-title>
            <v-row justify="space-between">
              <v-col cols="12" sm="4">
                Ввод и вывод
              </v-col>
              <v-col cols="12" sm="4">
                <v-select
                  label="Категория" 
                  :items="groups2"
                  v-model="group_type2"
                ></v-select>
              </v-col>
              <v-col cols="12" sm="4">
                <v-text-field
                  v-model="search2"
                  append-icon="mdi-magnify"
                  label="Поиск"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card-title>
          <v-data-table
            :headers="headers2"
            :items="filteredItems2"
            :search="search2"
          >
            <template v-slot:item.type="props">
              <v-edit-dialog
                :return-value.sync="props.item.type"
                large
                persistent
                @save="save"
                @cancel="cancel"
                @open="open"
                @close="close"
              >
                <span>{{ props.item.type }}</span>
                <template v-slot:input>
                  <v-text-field
                    v-model="props.item.type"
                    label="Edit"
                    single-line
                    counter
                    autofocus
                  ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>
            <template v-slot:item.date="props">
              <v-edit-dialog
                :return-value.sync="props.item.date"
                large
                persistent
                @save="save"
                @cancel="cancel"
                @open="open"
                @close="close"
              >
                <span>{{ props.item.date }}</span>
                <template v-slot:input>
                  <v-text-field
                    v-model="props.item.date"
                    label="Edit"
                    single-line
                    counter
                    autofocus
                  ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>
            <template v-slot:item.amount="props">
              <v-edit-dialog
                :return-value.sync="props.item.amount"
                large
                persistent
                @save="save"
                @cancel="cancel"
                @open="open"
                @close="close"
              >
                <span>{{ props.item.amount }}</span>
                <template v-slot:input>
                  <v-text-field
                    v-model="props.item.amount"
                    label="Edit"
                    single-line
                    counter
                    autofocus
                  ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>
            <template v-slot:item.status="props">
              <v-edit-dialog
                :return-value.sync="props.item.status"
                large
                persistent
                @save="save"
                @cancel="cancel"
                @open="open"
                @close="close"
              >
                <span>{{ props.item.status }}</span>
                <template v-slot:input>
                  <v-text-field
                    v-model="props.item.status"
                    label="Edit"
                    single-line
                    counter
                    autofocus
                  ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>
            <template v-slot:item.action>
              <v-btn icon>
                <v-icon small>
                  mdi-close-circle-outline
                </v-icon>
              </v-btn>
            </template> 
          </v-data-table>

          <v-snackbar 
            v-model="snack" 
            :timeout="3000" 
            :color="snackColor"
          >
            {{ snackText }}
            <v-btn 
              text 
              @click="snack = false"
            >
              Close
            </v-btn>
          </v-snackbar>
        </v-card>
      </v-col>
      <v-col>
        <v-card 
          max-width="700" 
          class="mx-auto mb-3"
        >
          <v-card-title>
            <v-row justify="space-between">
              <v-col cols="12" sm="4">
                История игр
              </v-col>
              <v-col cols="12" sm="4">
                <v-select
                  label="Категория" 
                  :items="groups"
                  v-model="group_type"
                ></v-select>
              </v-col>
              <v-col cols="12" sm="4">
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Поиск"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card-title>
          <v-data-table
            :headers="headers"
            :items="filteredItems"
            :search="search"
          >
            <template v-slot:item.gameId="props">
              <v-edit-dialog
                :return-value.sync="props.item.gameId"
                large
                persistent
                @save="save"
                @cancel="cancel"
                @open="open"
                @close="close"
              >
                <span>{{ props.item.gameId }}</span>
                <template v-slot:input>
                  <v-text-field
                    v-model="props.item.gameId"
                    label="Edit"
                    single-line
                    counter
                    autofocus
                  ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>
            <template v-slot:item.date="props">
              <v-edit-dialog
                :return-value.sync="props.item.date"
                large
                persistent
                @save="save"
                @cancel="cancel"
                @open="open"
                @close="close"
              >
                <span>{{ props.item.date }}</span>
                <template v-slot:input>
                  <v-text-field
                    v-model="props.item.date"
                    label="Edit"
                    single-line
                    counter
                    autofocus
                  ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>
            <template v-slot:item.status="props">
              <v-edit-dialog
                :return-value.sync="props.item.status"
                large
                persistent
                @save="save"
                @cancel="cancel"
                @open="open"
                @close="close"
              >
                <span>{{ props.item.status }}</span>
                <template v-slot:input>
                  <v-text-field
                    v-model="props.item.status"
                    label="Edit"
                    single-line
                    counter
                    autofocus
                  ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>
            <template v-slot:item.chanceOfVictory="props">
              <v-edit-dialog
                :return-value.sync="props.item.chanceOfVictory"
                large
                persistent
                @save="save"
                @cancel="cancel"
                @open="open"
                @close="close"
              >
                <span>{{ props.item.chanceOfVictory }}</span>
                <template v-slot:input>
                  <v-text-field
                    v-model="props.item.chanceOfVictory"
                    label="Edit"
                    single-line
                    counter
                    autofocus
                  ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>
            <template v-slot:item.action>
              <v-btn icon>
                <v-icon small>
                  mdi-close-circle-outline
                </v-icon>
              </v-btn>
            </template> 
          </v-data-table>

          <v-snackbar 
            v-model="snack" 
            :timeout="3000" 
            :color="snackColor"
          >
            {{ snackText }}
            <v-btn 
              text 
              @click="snack = false"
            >
              Close
            </v-btn>
          </v-snackbar>
        </v-card>
      </v-col>
      <v-col>
        <v-card 
          max-width="700"
          class="mx-auto"
          >
          <v-card-title>Общая статистика</v-card-title>
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">Победы</th>
                  <th class="text-left">Поражения</th>
                  <th class="text-left">Депозит</th>
                  <th class="text-left">Вывод</th>
                  <th class="text-left">Баланс за все время</th>
                  <th class="text-left">Дата регистрации</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td v-for="(stat, index) of stats" :key="index">{{ stat }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>    
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  export default {
    props: ['id'],
    data: () => ({
      search: '',
      search2: '',
      snack: false,
      snackColor: '',
      snackText: '',
      groups: ['All', 'Winning', 'Losing', 'Process'],
      groups2: ['All', 'Deposit', 'Withdraw'],
      group_type: null,
      group_type2: null,
      roles: ['admin', 'moderator', 'user'],
      headers: [
        { text: 'ID игры', align: 'left', value: 'gameId' },
        { text: 'Дата', value: 'date' },
        { text: 'Статус', value: 'status' },
        { text: 'Шанс победы', value: 'chanceOfVictory' },
        { text: 'Действия', value: 'action', sortable: false }
      ],
      headers2: [
        { text: 'Тип', align: 'left', value: 'type' },
        { text: 'Дата', value: 'date' },
        { text: 'Сумма', value: 'amount' },
        { text: 'Статус', value: 'status' },
        { text: 'Действия', value: 'action', sortable: false }
      ],
      data: [
        { 
          gameId: 1,
          date: new Date().toISOString().substr(0, 10),
          status: 'winning',
          chanceOfVictory: 0.4
        },
        { 
          gameId: 2,
          date: new Date().toISOString().substr(0, 10),
          status: 'losing',
          chanceOfVictory: 0.1   
        },
        { 
          gameId: 3,
          date: new Date().toISOString().substr(0, 10),
          status: 'winning',
          chanceOfVictory: 0.92 
        },
        { 
          gameId: 4,
          date: new Date().toISOString().substr(0, 10),
          status: 'process',
          chanceOfVictory: 'process'
        },
        { 
          gameId: 5,
          date: new Date().toISOString().substr(0, 10),
          status: 'winning',
          chanceOfVictory: 0.64 
        }        
      ],
      data2: [
        { 
          type: 'deposit',
          date: new Date().toISOString().substr(0, 10),
          status: 'done',
          amount: 400
        },
        { 
          type: 'deposit',
          date: new Date().toISOString().substr(0, 10),
          status: 'done',
          amount: 320
        },
        { 
          type: 'deposit',
          date: new Date().toISOString().substr(0, 10),
          status: 'done',
          amount: 150
        },
        { 
          type: 'withdraw',
          date: new Date().toISOString().substr(0, 10),
          status: 'done',
          amount: 1100
        },
      ],
      user: {
        userName: 'Marcus Obrien',
        userRole: 'moderator',
        balance: 1000
      },
      stats: {
        victory: 10,
        losing: 2,
        deposit: 1200,
        withdrawal: 2000,
        balance: 6500,
        date: new Date().toISOString().substr(0, 10)
      }
    }),
    methods: {
      save () {
        this.snack = true;
        this.snackColor = 'success';
        this.snackText = 'Данные обновлены';
      },
      cancel () {
        this.snack = true;
        this.snackColor = 'error';
        this.snackText = 'Отмена';
      },
      open () {
        this.snack = true;
        this.snackColor = 'info';
        this.snackText = 'Редактирование';
      },
      close () {
        console.log('Dialog closed');
      }
    },
    computed: {
      filteredItems() {
        return this.data.filter((item) => {
          return (this.group_type == 'All') ? item : !this.group_type || (item.status.toLowerCase() === this.group_type.toLowerCase());
        });
      },
      filteredItems2() {
        return this.data2.filter((item) => {
          return (this.group_type2 == 'All') ? item : !this.group_type2 || (item.type.toLowerCase() === this.group_type2.toLowerCase());
        });
      },
    }
  }
</script>
